let artid, _this;
export default {
	data() {
		return {
			article: [],
			artPhoto: [],
			yangSkeleton: 'ing'
		}
	},
	onLoad(options) {
		_this = this;
		artid = options.art_id;
		_this.getArticleInfo();
		_this.setView();
	},
	methods: {
		getArticleInfo() {
			uni.showLoading({
				title: "正在加载..."
			})
			uni.request({
				url: _this.apiServer + "art&m=infoWithUser",
				data: {
					artid: artid
				},
				success(res) {
					let data = res.data.data;
					//勾画骨架
					setTimeout(function() {
						for (let i = 0; i < data.photo.length; i++) {
							_this.artPhoto.push(_this.staticServer + data.photo[i].content);
						}
						_this.article = {
							art_id: data.art_id,
							u_name: data.u_name,
							u_face: data.u_face,
							art_title: data.art_title,
							art_createtime: data.art_createtime,
							art_content: data.art_content
						}
						_this.yangSkeleton = 'end';
						uni.hideLoading()
					}, 500);
				}
			})
		},
		showImgs(e) {
			let url = e.currentTarget.dataset.url;
			uni.previewImage({
				urls: _this.artPhoto,
				current: url
			})
		},
		setView() {
			uni.request({
				url: _this.apiServer + "art&m=view",
				data: {
					artid: artid
				},
				success(res) {

				}
			})
		}
	}

}
